@mixin direction-hover($color, $direction: 'to bottom') {
  @media (hover: hover) {
    &:hover:not(.focus-visible) {
      background-color: $color;
      top: -6px;

      &::after {
        opacity: 1;
        @if $direction != 'to bottom' {
          background-image: linear-gradient($direction, transparent 30%, $color 96%);
        } @else {
          background-image: linear-gradient(transparent 45%, $color 84%);
        }
      }
      @content;
    }
  }
}

@mixin retina {
  @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
    @content;
  }
}

@mixin hover {
  @media (hover: hover) {
    &:hover:not(.focus-visible) {
      @content;
    }
  }
}

@mixin focus {
  &.focus-visible:focus {
    @content;
  }
}

@mixin active {
  &.focus-visible:active {
    @content;
  }
}

@mixin hover-focus {
  @media (hover: hover) {
    &:hover:not(.focus-visible) {
      @content;
    }
  }

  &.focus-visible:focus {
    @content;
  }
}

// Desktop first

@mixin vp-1919 {
  @media (max-width: ($vp-1920 - 1px)) {
    @content;
  }
}

